.myDiv {
    width: 98vw;
    height: 100vh;
    overflow: hidden;
    background-color: aqua;
    font-size: 10px;
    padding: 2rem;
    border: 1px solid #000;
    -webkit-box-sizing: border-box; 
    -moz-box-sizing: border-box;    
    box-sizing: border-box;    
}

.parent {
    background-color: aqua;
    position: relative;
    display: block;
}

.child {
    position: absolute;
    display: block;
}
.BigDaddy {
    background-color: red
}